




























    import { mapState, mapMutations, mapGetters } from 'vuex';
    export default {
    data() {
        return {
            outcome:"",
            showClearIcon: false,
            inputClearValue: '',
            items: ['找人', '找群', '找公众号'],
            styles: [
                {
                    value: 'text',
                    text: '文字',
                    checked: true
                },
                {
                    value: 'button',
                    text: '按钮'
                }
            ],
            colors: ['#007aff', '#4cd964', '#dd524d'],
            current: 0,
            activeColor: '#007aff',
            styleType: 'text'
        };
    },
    computed: {
        wrapStyle() {
            let styleString = '';
            switch (this.styleType) {
                case 'text':
                    styleString = `border:0;`;
                    break;
                default:
                    styleString = `border-color: ${this.activeColor}`;
                    break;
            }
            return styleString;
        },
        itemStyle() {
            let styleString = '';
            switch (this.styleType) {
                case 'text':
                    styleString = `color:#000;border-left:0;`;
                    break;
                default:
                    styleString = `color:${this.activeColor};border-color:${this.activeColor};`;
                    break;
            }
            return styleString;
        },
        activeStyle() {
            let styleString = '';
            switch (this.styleType) {
                case 'text':
                    styleString = `color:${
                        this.activeColor
                    };border-left:0;border-bottom-style:solid;`;
                    break;
                default:
                    styleString = `color:#fff;border-color:${this.activeColor};background-color:${
                        this.activeColor
                    }`;
                    break;
            }
            return styleString;
        }
    },
    methods: {
        ...mapMutations(['getActiveId','setStranger']),
        toman(man) {
            this.getActiveId(man.id)
            this.setStranger(man)
            uni.navigateTo({
                url: '../person/person'
            })
        },
        searchfriend() {
            if (this.inputClearValue.length) {
                uni.showLoading({
                    title: '加载中'
                });
                const url =
                    'http://zhangzibao.xyz:8080/myqqServer/api/Alluser/' + this.inputClearValue;
                uni.request({
                    url: url,
                    method: 'GET'
                }).then(([err, res]) => {
                    if (res.data.length) {
                        this.toman(res.data[0])
                    } else {
                        this.outcome = "没有找到结果"
                    }
                    uni.hideLoading()
                });
            }
        },
        onClickItem(index) {
            if (this.current !== index) {
                this.current = index;
            }
        },
        bindClearInput: function(e) {
            this.inputClearValue = e.target.value;
            this.showClearIcon = e.target.value.length > 0;
        },
        clearIcon: function() {
            this.inputClearValue = '';
            this.showClearIcon = false;
            this.outcome = ""
        }
    }
};
